@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}
body {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.header {
  width: 100vw;
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.header__container {
  width: 90vw;
  padding-left: 5vw;
  padding-right: 5vw;
  padding-top: 1vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__container .header__logo {
  width: 11vw;
  height: 5vh;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .header__container {
    padding-top: 0;
    padding-right: 0;
    padding-top: 2vh;
  }
  .header__container .header__logo {
    width: 25vw;
    height: 8vh;
  }
  .header__container .header__title {
    display: none;
  }
  .header__container .header__menuLink {
    display: none;
  }
}
@media (min-width: 1024px) {
  .header__container {
    padding-top: 0;
    padding-right: 0;
    width: 50vw;
    padding-top: 2vh;
  }
  .header__container .header__title {
    display: none;
  }
  .header__container .header__menuLink {
    display: none;
  }
}
.header__menu {
  width: 100vw;
  display: flex;
  flex-direction: column;
}
.header__menu .header__point {
  width: 100vw;
  padding-top: 1vh;
  padding-bottom: 1vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #F5F5F5;
}
.header__menu .header__point .header__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  font-weight: 500;
  font-family: "Oswald";
}

@media (min-width: 768px) and (max-width: 1023px) {
  .header__menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-right: 5vw;
    padding-top: 2vh;
  }
  .header__menu .header__point {
    border-top: none;
  }
  .header__menu .header__point .header__link {
    font-size: 18px;
    line-height: 25px;
    color: #222222;
    font-weight: 400;
  }
}
@media (min-width: 1024px) {
  .header__menu {
    width: 45vw;
    display: flex;
    flex-direction: row;
    padding-right: 5vw;
    padding-top: 2vh;
  }
  .header__menu .header__point {
    border-top: none;
  }
  .header__menu .header__point .header__link {
    font-size: 20px;
    line-height: 25px;
    color: #222222;
    font-weight: 400;
  }
}